const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 10,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.mode === 'light' ? '#1a90ff' : '#308fe8',
  },
}));

const OrderStatusMessage = (props) => {
  const {message} = props;
    return (
    <div class='dialogue dialogue-chat'>
      <Card sx={{ border: "none", boxShadow: "none", height:'auto',}}>
      <Typography gutterBottom variant="h6" component="div">
          {`Order is on the way! Estimated delivery in 10 minutes`}
          </Typography>
        <CardMedia
          style={{objectFit: 'contain',height:'auto', width:'100%'}}
          image={'gopuff-map.png'}
          component="img"
        />
     </Card>
     </div>
    )
  }

const UserMessageItem = (props) => {

  return (<div class='dialogue dialogue-user'>
    <Typography variant="body1" color="text.secondary">
    {props.message}
    </Typography>
    </div>)
}
  
const ChatGptMessageItem = (props) => {
  const {message} = props;
  if(message.isProduct){
    return (
    <div class='dialogue dialogue-chat'>
      <Card sx={{ border: "none", boxShadow: "none", height:'auto',}}>
      <Typography gutterBottom variant="body1" component="div">
          {message.product.blurb}
          </Typography>
        <CardMedia
          style={{objectFit: 'contain',height:200, width:'100%'}}
          image={message.product.image}
          component="img"
        />
        <CardContent>
          <Typography gutterBottom variant="body1" component="div">
          {message.product.name}
          </Typography>
          <Typography noWrap variant="h6" color="text.secondary">
          ${message.product.price}
          </Typography>
          <Typography  variant="shouldBold" color="text.secondary">
          {message.product.description}
          </Typography>
        </CardContent>
        <CardActions>
          <ButtonGroup size="large" variant="text" aria-label="text button group">
            <Button onClick={()=> props.onAddToCart(message.product.name)}>Add To Cart</Button>
            <Button onClick={props.onSuggestAnother}>Suggest Another</Button>
          </ButtonGroup>
        </CardActions>
     </Card>
    </div>)
  }
  if(message.shouldBold){
    return (
    <div class='dialogue dialogue-chat'>
          <Typography variant="h6" color="text.secondary">
          <b>
          {message.message}
          </b>
          </Typography>
      </div>)
  }
  return (
  <div class='dialogue dialogue-chat'>          
    <Typography variant="body1" color="text.secondary">
    <b>{message.message}</b>
    </Typography>
    </div>)
  
  }
  
  
  
  const MessageList = (props) => {
  
    const [value, setValue] = useState(props.messages);
    return (
    <div key={value}>
      {value.map(item => {
              if(item.isOrderStatus){
                return <OrderStatusMessage />
              }
              if(item.isUser){
                return <UserMessageItem onAddToCart={props.onAddToCart} message={item.message} />
              }else{
                return <ChatGptMessageItem onAddToCart={props.onAddToCart} message={item} onSuggestAnother={props.onSuggestAnother} />
              }

  })}
    </div>
    
    );
  }
  
  
